@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  background: #fff;
  box-shadow: 1px 1px 7px hsla(0, 0%, 20%, .7);
  /* margin-bottom: 2rem; */
}

.nav-list {
  width: 70px;
  overflow: hidden;

  width: auto;
  overflow: visible;
  height: 85px;

  position: relative;
  top: 10px;
  text-align: right;
  right: 0;
  min-width: 170px;
  font-size: .95em;
}

.top-links {
  height: 1rem;
  background: var(--main-blue);
  color: #fff;
  height: 32px;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 2% 0 5%;
  margin-top: 2rem;
  justify-content: space-between;
}

.top-links div {
  margin: 0 1rem;
  width: auto;
}

.top-links a {
  color: #fff;
  text-decoration: none;
}

.email-link {
  font-weight: 300;
  font-size: .8rem;
  letter-spacing: .05rem;
  height: 100%;
  display: flex;
  align-items: center;
  background: var(--lt-blue);
  padding: 0 .5rem;
  display: none;
}

.phone-link,
.login-link,
.signup-link {
  font-size: .9rem;
}

.phone-link {
  position: absolute;
  top: .4rem;
  color: var(--main-blue);
  color: #fff;
  left: 20%;
  transform: translateX(-60%);
}

.phone-link a {
  color: var(--main-blue);
  color: #fff;
}

.login-link {
  margin-left: 0rem !important;
}

.signup-link {
  margin: 0 2rem !important;
}

.top-links i {
  font-size: 12px;
  margin-right: .3rem;
}

.logo {
  height: 93px;
  width: 201px;
  background: url(/wholesale-hvac/static/media/logo-RR-cropped.1129a128.png) no-repeat;
  background-size: contain;
  position: relative;
  top: -23px;
  left: 0;

  position: relative;
  left: auto;
  transform: translateX(0);
  height: 130px;
  top: 5px;
}

.short-header .logo {
  top: 8px;
  height: 76px;
  width: 160px;
  background: url(/wholesale-hvac/static/media/logo-RR-cropped.1129a128.png) no-repeat;
  background-size: contain;
}

.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1210;
  width: 96%;
  margin: 0.4rem auto auto;
  font-size: .89rem;
  font-weight: 900;
  position: relative;
  height: 140px;
}

.short-header .main-nav {
  height: 98px;
  margin: -1rem auto auto;
}

.menu-icon {
  font-size: 28px;
  display: block;
  color: var(--lt-blue);
  height: 30px;
  transition: 1s;
  overflow: hidden;

  display: none;
}

.nav-list ul {
  list-style: none;
  padding: 0;
  margin: 0.8rem 0.8rem 0 0;
  max-width: 89%;
  line-height: 1rem;
  float: right;
}

.nav-list li {
  margin: 0;
  line-height: 1.1rem;
  font-size: .9rem;
}

.nav-list li a.selected {
  color: var(--hi-blue);
}

.nav-list li a:hover {
  color: var(--lt-blue-trans);
}

.short-header .nav-list ul {
  margin: 0rem 8.6rem 0 0;
}

.link-home {
  display: none;
}

.link-shop {
  padding-top: 0.4rem;
  ;
}

.nav-list .nobreak {
  display: inline-block;
}

.quote-button {
  top: 5px;
  position: absolute;
  right: 0;
}

.short-header .quote-button {
  top: 19px;
  right: -8px;
}

.quote-button a {
  height: 1rem;
  background: var(--nav-hover);
  color: #fff;
  height: 32px;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  justify-content: center;
}

/*    ########    MEDIA QUERIES    ########  */

@media screen and (min-width: 410px) {
  .top-links {
    margin-top: 0;
    justify-content: flex-start;
  }

  .phone-link {
    position: relative;
    color: #fff;
    top: 0;
    left: 0rem;
    transform: translateX(0);
  }

  .phone-link a {
    color: #fff;
  }

  .login-link {
    margin-left: calc(100% - 330px) !important;
    margin-left: 1rem;
  }

  .signup-link {
    margin: 0 !important;
  }

  .nav-list ul {
    max-width: 100%;
  }

  .nav-list li {
    line-height: 1.3rem;
  }
}

@media screen and (min-width: 507px) {
  .email-link {
    display: flex;
  }
}

@media screen and (max-width: 874px) {
  .payzer-button {
    top: 43px !important;
    transform: scale(0.47) !important;
    right: -4rem !important;
    transition: .45s;
  }

  .payzer-button:hover {
    transform: scale(0.62) !important;
  }
}

@media screen and (min-width: 699px) {



  .login-link {
    margin-left: calc(100% - 630px) !important;
  }
}

@media screen and (min-width: 875px) {

  .short-header .main-nav {
    height: 80px;
  }

  .short-header .logo {
    position: relative;
    left: auto;
    transform: translateX(0);

    top: 8px;
    height: 60px;
    background: url(/wholesale-hvac/static/media/logo-RR-cropped.1129a128.png) no-repeat;
    background-size: contain;
  }

  .nav-list {
    width: 100%;
    height: 75px;
    top: 19px;
  }

  .quote-button {
    top: 5px;
    position: absolute;
    right: 0;
  }

  .short-header .quote-button {
    top: 30px;
    position: absolute;
    right: -8px;
  }

  .short-header .nav-list {
    top: 32px;
  }

  .short-header .nav-list ul {
    margin-right: 8.3rem;
  }

  .menu-icon {
    display: none;
  }

  .nav-list ul {
    flex-direction: row;
  }

  .nav-list li {
    display: inline-block;

    margin: 0 0.4rem 0.1rem;
    line-height: 1.3rem;
    font-size: 0.9rem;
  }

  .nav-list li.link-home {
    display: none;
  }


}

@media screen and (min-width: 978px) {

  .nav-list li.link-home {
    display: inline-block
  }

  .link-shop {
    margin-top: 0;
  }

}


@media screen and (max-width: 499px) {
  .nav-list {
    width: 70px;
    overflow: hidden;
    width: auto;
    overflow: hidden;
    height: 105px;
    position: absolute;
    top: 30px;
    text-align: right;
    right: 0;
    min-width: 170px;
    font-size: .8rem;
    line-height: 1.5;
  }

  .short-header .nav-list {
    top: 16px;
  }

  .short-header .nav-list ul {
    margin: 0 0.8rem 0 0;
  }

  .short-header .quote-button {
    top: -18px;
  }

  .logo {
    width: 150px;
    top: -5px;
  }

  .payzer-button {
    top: 83px !important;
    transform: scale(0.47) !important;
    right: -63px !important;
    transition: .45s;
  }
}
@supports (display: grid) {
  .info-grid {
    margin-top: -2rem;
    display: grid;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    min-height: 350px;
    /* padding: 1.5vw;
    margin: 2vmin; */
    /* border: 10px solid #666; */
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
    


    grid-template-areas:
      "title   title   title title title"
      ".   .  product-image  .  ."
      "pricing-info   pricing-info  pricing-info  pricing-info  pricing-info"
      "pricing-info   pricing-info  pricing-info  pricing-info  pricing-info"
      "pricing-info   pricing-info  pricing-info  pricing-info  pricing-info"
      "brand-logos    brand-logos   brand-logos   brand-logos   brand-logos "
      "finance-btns   finance-btns   finance-btns  finance-btns  finance-btns"
      "footer         footer        footer        footer        footer      ";

  top: 25%;
  }

  .price-grid.hide {
    display: none;
  }

  .pricing-info-box {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .pricing-info-row {
    display: flex;
    flex-direction: column;
    border-bottom: 2px #999 solid;
    width: 100%;
    /* border-left: 2px #999 solid; */
  }

  .pricing-info-row.platinum {
    background-image: linear-gradient(55deg, hsla(170, 10%, 50.6%, .0), hsla(170, 10%, 50.6%, .6), hsla(170, 10%, 50.6%, .2), hsla(170, 10%, 50.6%, .4), hsla(170, 10%, 50.6%, .2), hsla(170, 10%, 50.6%, .2), hsla(170, 10%, 50.6%, .3), hsla(170, 10%, 50.6%, 0.2), hsla(170, 10%, 50.6%, .4));
  }

  .pricing-info-row.gold {
    background-image: linear-gradient(55deg, hsla(65, 60%, 50.6%, .0), hsla(65, 60%, 50.6%, .6), hsla(65, 60%, 50.6%, .2), hsla(65, 60%, 50.6%, .4), hsla(65, 60%, 50.6%, .2), hsla(65, 60%, 50.6%, .2), hsla(65, 60%, 50.6%, .3), hsla(65, 60%, 50.6%, 0.2), hsla(65, 60%, 50.6%, .4));
  }

  .pricing-info-row.silver {
    background-image: linear-gradient(55deg, hsla(210, 15%, 82.6%, .0), hsla(210, 15%, 82.6%, .6), hsla(210, 15%, 82.6%, .2), hsla(210, 15%, 82.6%, .4), hsla(210, 15%, 82.6%, .2), hsla(210, 15%, 82.6%, .2), hsla(210, 15%, 82.6%, .3), hsla(210, 15%, 82.6%, 0.2), hsla(210, 15%, 82.6%, .4));
    border-bottom: 0 none;
  }

  .pricing-info-row .cell {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .level-name,
  .level-price {
    flex-grow: 1;
  }

  .level-info {
    flex-grow: 2;
  }

  .pricing-level,
  .seer {
    margin: 0;
  }

  .level-features li {
    text-align: left;
    font-size: 0.9rem;
  }

  .level-description {
    font-weight: 900;
  }


  .info-grid .title {
    grid-area: title;
  }

.info-grid .product-image {
    grid-area: product-image;
    position: relative !important;
    z-index: 10;
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    margin: 0rem auto -15px;
    top: 95px;
    left: 0;
    transform: translate(0,-50%);
    min-width: 160px;
    max-height: 200px;
    padding-left: 22rem;
}

.info-grid .product-image img {
    max-width: 100%;
    position: relative;
    top: 0;
    left: auto;
    z-index: 10;
    margin: .8rem;
    height: 130px;
}

  .info-grid .pricing-info {
    grid-area: pricing-info;
  }

  .info-grid .right-sidebar {
    grid-area: right-sidebar;
  }

  .info-grid .finance-btn1 {
    grid-area: finance-btn1;
  }

  .info-grid .brand-logos {
    grid-area: brand-logos;
  }

  .info-grid .brand-logos img {
    max-width: 100%;
    display: block;
  }

  .info-grid .finance-btn2 {
    grid-area: finance-btn2;
  }

  .info-grid .mobile-finance-buttons {
    grid-area: finance-btns;
  }

  .info-grid .footer {
    grid-area: footer;
  }

}


*,
*:before,
*:after {
  box-sizing: border-box;
}

.info-grid .cell {
  position: relative;
  padding: 1vw;
  background-position: left bottom;
  background-size: 150% 100%;
  transition: background 500ms, outline 500ms;
  background-position: top left;
  outline: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.pricing-info {
  margin: 1rem;
  padding-bottom: 2rem;
}

.info-grid .title {
  background-image: linear-gradient(55deg, #f00 0%, #ff7800 60%);
  color: #fff;
  font-size: 1.3rem;
  font-weight: 500;
  padding: 1.5rem;
}

.price-month,
.level-tot-price {
  font-weight: 900;
}

.apply-button {
  display: flex;
  padding: 0.5rem;
  color: #fff;
  background-image: linear-gradient(55deg, #444 0%, #222 60%);
  border-radius: 0.4rem;
  font-weight: 900;
  text-decoration: none;
  min-height: 5rem;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.finance-btn1,
.finance-btn2 {
  position: relative;
}

.finance-btn1 .hover-show,
.finance-btn2 .hover-show {
  /* display: none; */
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(75%, -70%);
  padding: 0.5rem;
  border-radius: 0.4rem;
  border: 1px #ccc solid;
  box-shadow: #333 2px 2px 2px;
  font-size: 11px;
  background: #fff;
  width: 240px;
  opacity: 0;
  -webkit-filter: opacity(0);
          filter: opacity(0);
  z-index: -10;
  transition: 500ms;
  height: auto;
}

.finance-btn2 .hover-show {
  transform: translate(-5%, -70%);
}

.finance-btn1:hover .hover-show,
.finance-btn2:hover .hover-show {
  /* display: block; */
  -webkit-filter: opacity(1);
          filter: opacity(1);
  opacity: 1;
  z-index: 10;
  height: auto;
}

.finance-btn1 .apply-button span {
  color: #2bc52b;
}

.finance-btn2 .apply-button span {
  color: #ff7800;
}

.info-grid footer.footer {
  display: flex;
  justify-content: space-between;
  padding: 4rem 0.5rem 0.5rem;
}

.info-grid footer.footer img {
  max-height: 60px;
}

.mobile-finance-buttons {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-around;
}


@media screen and (min-width: 740px) {
  .info-grid {
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
    grid-template-areas:
      "title            title         title           title         title"
      "product-image    pricing-info  pricing-info    pricing-info  pricing-info"
      "brand-logos    brand-logos  brand-logos     brand-logos   brand-logos"
      "finance-btns      finance-btns   finance-btns     finance-btns   finance-btns"  
      "footer           footer        footer          footer        footer";
  }

  .pricing-info-row {
    flex-direction: row;
  }

  .info-grid .product-image {
    position: relative !important;
    /* top: 125px;
    left: -15px; */
    z-index: 10;
    margin: 3rem auto;
  }



}

input,
select {
    height: 1.5rem;
    font-size: 1rem;
    font-family: Montserrat, arial, sans-serif
}

input[type='submit'] {
    background-color: #28bcff;
    border: none;
    color: #000;
    padding: 1rem 1.5rem;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
    height: 3rem;
}

.SystemRec .step4 {
    margin-top: 1rem;
    overflow: hidden;
}

.Replacements .step4 {
    margin-top: 1.9rem;
    overflow: visible;
}

/* .RecommendedReplacements  */
input[type="radio"] {
    display: none;
}

/* .RecommendedReplacements  */

.RecommendedReplacements .step4 {
    overflow: visible;
    height: 9rem;
    padding: 1rem 1rem 1.5rem;
}

.RecommendedReplacements .step4 fieldset {
    padding: 1rem 1rem 4.7rem;
}

fieldset section {
    display: flex;
    justify-content: center;
}

.step {
    height: 0;
    overflow: hidden;
}

.step fieldset label,
.step fieldset select,
.step fieldset input[type=text] {
    border: 1px solid #ccc;
    padding: .1rem .3rem;
    border-radius: 5px;
    background: #fff;
    font-size: .9rem;
    font-weight: 600;
    display: inline-flex;
    position: relative;
    height: 2.4rem;
    margin: 0 0.5rem;
    align-items: center;
    width: 40%;
    justify-content: center;
    line-height: 1rem;
}

.step fieldset label.selected ,
.step fieldset select.selected ,
.step fieldset input[type=text].selected  {
    border: 3px solid var(--hi-blue);
}



.Replacements fieldset label {
    border: 1px solid #ccc;
    padding: .1rem .3rem;
    border-radius: 5px;
    background: #fff;
    font-size: .9rem;
    font-weight: 600;
    display: inline-flex;
    position: relative;
    height: 2.4rem;
    margin: 0 0.5rem;
    align-items: center;
    /* width: 40%; */
    justify-content: center;
}

.Replacements .nowrap {
    white-space: nowrap;
    margin-bottom: 1rem;
    height: 120px;
}

label.selected {
    color: #222;
    border: 3px solid var(--hi-blue);
    /* background: var(--trans-blue); */
}


.main-graphic, .medallion-graphic {
    width: 100%;
    max-width: 850px;
    margin: 1rem auto auto 50%;
    transform: translateX(-50%);
    transition: .8s;
    opacity: 1;
    -webkit-filter: opacity(1);
            filter: opacity(1);
    top: 10rem;
    height: auto;
}

.medallion-graphic {
    display: flex;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 2.5rem;
}

.medallion-graphic div {
    display: flex;
    width: 100%;
    justify-content: center;
}
.medallion-graphic p {
    width: 100%;
    display: block;
    text-align: center;
    font-size: 1.2rem;
    font-weight: 900;
}

.medallion-graphic div img {
    height: 15vw;
    min-height: 100px;
    max-height: 100%;
    width: auto;
}

.recommendation-finder {
    color: rgb(94, 94, 94);
    font-family: Montserrat, arial, sans-serif;
    font-weight: 400;
    /* max-width: 1150px; */
    background: #fff;
    width: 100%;
    position: relative;
    /* overflow: hidden; */
}

.recommendation-finder .fieldset {
    border-radius: 14px;
    text-align: center;
}

.recommendation-finder .active .fieldset DONTSHOW {
    box-shadow: #00aeef 0px 3px 0px;
}

form,
.recommendation {
    width: 0;
    padding: 0 0 3px;
    margin: auto;
    overflow: hidden;
    transition: 0.8s;
    text-align: left;
    opacity: 1;
    -webkit-filter: opacity(1);
            filter: opacity(1);
}

.sysRec-intro {
    overflow: hidden;
    transition: 1s;
    height: auto;
}

.sysRec-intro.hide {
    height: 0;
}

.step1 {
    transition: top .5s;
    height: auto;
    position: relative;
    left: auto;
    top: auto;
}

.step1.hide {
    height: 0;
}

.main-graphic.hide, .medallion-graphic.hide {
    opacity: 0;
    -webkit-filter: opacity(0);
            filter: opacity(0);
    position: absolute;
    top: 10rem;
    overflow: hidden;
    height: 0;
}

/* .step1.hide * {
    opacity: 0;
    height: 0;
} */

.step2 {
    transition: 0.7s 0.21s;
}

.step1 input,
.step2 label,
.step3 select,
.SystemRec:not('.Replacements') .step4 label {
    border: 1px #ccc solid;
    padding: .1rem .3rem .1rem 0.6rem;
    border-radius: 5px;
    /* color: #333; */
    background: #fff;
    font-size: .9rem;
    font-weight: 600;
    display: inline-flex;
    position: relative;
    height: 2.4rem;
    margin: 0 0 0 1vw;
    align-items: center;
    width: 20vw;
    min-width: 15rem;
}

fieldset,
.fieldset {
    padding: 1rem;
    position: relative;
}

/* legend {
    text-align: left;

} */

legend {
    font-family: Montserrat, arial, sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 25px;
}

label .thumb-pic {
    width: 90px;
    height: 90px;
    object-fit: contain;
    position: absolute;
    top: 1.7rem;
}

label:hover {
    transform: scale(1.05);
}

label:hover .thumb-pic {
    width: 96px;
    height: 96px;
    top: 1.5rem;
    left: -1px;
}

form.active {
    border-radius: 20px;
    opacity: 1;
    -webkit-filter: opacity(1);
    filter: opacity(1);
    max-width: 550px;
}

form.step.hide {
    opacity: 0;
    -webkit-filter: opacity(0);
            filter: opacity(0);
}

.active legend {
    color: #007fb0
}

.show {
    width: 90%;
    height: auto;
}

.recommendation {
    position: absolute;
    width: 100vw;
    height: 0;
    left: 0;
    top: -40px;
    overflow: auto;
    background: #fff;
    z-index: 10;
    opacity: 0;
    -webkit-filter: opacity(0);
            filter: opacity(0);
    transition: 0.5s;
    padding: 0;
    box-sizing: border-box;
}

.recommendation.show {
    width: calc(100vw - 2rem);
    max-width: calc(100% - 2rem);
    height: calc(100vh - 155px);
    padding: .5rem;
    opacity: 1;
    -webkit-filter: opacity(1);
    filter: opacity(1);
    border: 1rem solid rgba(0, 0, 0, .4);
    margin: 1rem;
