.close-modal {
    position: -webkit-sticky;
    position: sticky;
    left: 95%;
    top: 1%;
    padding: 0;
    background: hsla(196, 100%, 47%, 1);
    color: #fff;
    z-index: 99;
    border: 3px hsla(196, 100%, 86%, 1) solid;
    height: 2rem;
    width: 2rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    line-height: 1em;
    justify-content: center;
    font-size: 1.2rem;
    border-radius: 9px;
    font-weight: 900;
    font-family: sans-serif;
    overflow: hidden;
}

.close-modal:before {
    content: "\2715";
    color: #fff;
    font-weight: 900;
}

.close-modal:hover {
    border: 3px hsla(196, 100%, 92%, 1) solid;
    background: hsla(196, 100%, 37%, 1);
    color: hsla(196, 100%, 87%, 1);
}

.hint {
    overflow: hidden;
    display: block;
    position: absolute;
    z-index: 999;
    background: var(--main-blue-trans);
    color: #333;
    transition: .5s;
    height: 0;
    width: 0;
    left: 0;
    top: 201px;
}

.hint span {
    width: 80%;
    height: auto;
    background: #fff;
    transform: translateY(-200px);
    padding: 2rem;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    min-height: 150px;
    margin: 260px auto;
    max-height: 50%;
    overflow: auto;
    display: block;
}

.hint span:before {
    content: "\2715";
    font-weight: 900;
    right: 0.5rem;
    position: absolute;
    top: 0.5rem;
    right: 1rem;
    color: #003;
    font-size: 1.5rem;
    color: #336;
    cursor: pointer;
}

.hint.show-hint {
    height: 100vh;
    width: 94vw;
    max-width: 1450px;
    margin: auto;
    left: 3vw;
}

@media screen and (min-width: 1450px) {
